.main {
  background: #0B1D26;
  position: relative;
  top: 100%;

  &::before {
    pointer-events: none;
    content: '';
    position: absolute;
    top: 1px;
    left: 0;
    width: 100%;
    height: 80px;
    transform: translate(0, -100%);
    background: linear-gradient(180deg, rgba(11, 29, 38, 0) 5%, #0B1D26);
  }
}
